// Create a vue component for smtp configuration /* * Trois niveaux : * * 1-
Composant chargeant les données pour le formulaire * 2- Composant pour gérer le
formulaire et sa validation * 3- Composant pour gérer les automatismes à
l'intérieur du container */
<script setup>
  import { ref, computed } from 'vue'
  import { useForm } from 'vee-validate'
  import { storeToRefs } from 'pinia'

  import FormFlatLayout from '@/layouts/FormFlatLayout.vue'

  import {
    useSmtpSettingsStore,
    useSmtpConfigStore,
  } from '@/stores/smtp_settings'

  import {
    getFieldData,
    getSubmitErrorCallback,
    getSubmitModelCallback,
  } from '@/helpers/form'
  import SmtpSettingsForm from './SmtpSettingsForm.vue'
  import Icon from '../Icon.vue'

  const props = defineProps({
    smtpSettingsId: { type: String, required: false },
    companyId: { type: String, required: false },
    defaultEmail: { type: String, required: false },
    formConfigUrl: { type: String, required: true },
    layout: { type: Object, default: FormFlatLayout },
  })

  const emit = defineEmits(['saved', 'cancel'])
  const loading = ref(true)
  const store = useSmtpSettingsStore()
  store.setInitialValues({
    smtpSettingsId: props.smtpSettingsId,
    companyId: props.companyId,
    defaultEmail: props.defaultEmail,
  })
  const isEdit = !!props.smtpSettingsId
  const formConfigStore = useSmtpConfigStore()
  formConfigStore.setUrl(props.formConfigUrl)

  async function preload() {
    const promises = [formConfigStore.loadConfig()]
    if (isEdit) {
      console.log("It's an edit view")
      promises.push(store.loadSmtpSettings(props.smtpSettingsId))
    }
    await Promise.all(promises)
    loading.value = false
  }

  const { smtpSettings } = storeToRefs(store)

  function onSaved(item) {
    emit('saved', item)
  }
  function onCancel() {
    emit('cancel')
  }
  async function onDelete() {
    await store.deleteSmtpSettings(props.smtpSettingsId)
    emit('cancel')
  }
  // Chargement (await toujours en fin de setup)
  await preload()
  // Chargement terminé
</script>

<template>
  <div class="main_toolbar action_tools" id="js_actions">
    <div class="layout flex main_actions">
      <div role="group"></div>

      <div role="group"></div>
      <div role="group">
        <button
          type="button"
          @click="onDelete"
          class="btn negative icon"
          :disabled="!isEdit"
          title="Effacer les paramètres SMTP"
          aria-label="Effacer les paramètres SMTP"
        >
          <Icon name="trash-alt" /> Effacer
        </button>
      </div>
    </div>
  </div>

  <div v-if="loading">Chargement des informations</div>
  <div class="limited_width width40" v-else>
    <div class="alert alert-info">
      Si vous configurez un serveur SMTP, les utilisateurs qui ont activé la
      fonctionnalité depuis leur fiche enseigne pourront envoyer des e-mails
      directement depuis le logiciel (par exemple pour envoyer des documents à
      leurs clients).
    </div>
    <SmtpSettingsForm
      :smtp-settings="smtpSettings"
      :layout="layout"
      :is-edit="isEdit"
      @saved="onSaved"
      @cancel="onCancel"
    />
  </div>
</template>
