<script async setup>
  import { ref, computed, watch, toRaw, provide } from 'vue'
  import { storeToRefs } from 'pinia'
  import { useForm, useFormValues, useSetFieldValue } from 'vee-validate'
  import * as yup from 'yup'

  import {
    useSmtpSettingsStore,
    useSmtpConfigStore,
  } from '@/stores/smtp_settings'
  import {
    formContextInjectionKey,
    getFieldData,
    getSubmitErrorCallback,
    getSubmitModelCallback,
    buildYupSchema,
  } from '@/helpers/form'
  import Icon from '../Icon.vue'
  import DebugContent from '../DebugContent.vue'
  import SmtpSettingsFormContent from './SmtpSettingsFormContent.vue'

  const props = defineProps({
    smtpSettings: { type: Object, required: true },
    layout: { type: Object, required: true },
    isEdit: { type: Boolean, required: true },
  })
  const emit = defineEmits(['saved', 'cancel', 'error'])
  const debug = DEBUG

  const Layout = props.layout
  const store = useSmtpSettingsStore()
  const formConfigStore = useSmtpConfigStore()

  const formSchema = computed(() => {
    console.log('Building form schema %s', 'default')
    const jsonSchema = formConfigStore.getSchema('default')
    console.log('Returning form schema')
    console.log(jsonSchema)
    return buildYupSchema(jsonSchema)
  })
  const {
    handleSubmit,
    values,
    errors,
    setErrors,
    isSubmitting,
    resetForm,
    setTouched,
    setFieldValue,
    setFieldTouched,
    validate,
  } = useForm({
    validationSchema: formSchema,
    initialValues: props.smtpSettings,
  })
  const onSubmitSuccess = getSubmitModelCallback(emit, store.saveSmtpSettings)
  const onSubmitError = getSubmitErrorCallback(emit)
  const onSubmit = handleSubmit(onSubmitSuccess, onSubmitError)

  const hasValidEmail = await yup
    .string()
    .email()
    .validate(props.smtpSettings?.sender_email)
  const showDetails = ref(hasValidEmail)

  // Message d'aide spécifique pour les FAIs
  const helpMessage = ref(false)
  const mailTestResult = ref(null)
  const canTestEmail = formConfigStore.getOptions('can_send_test_email')

  // Utilitaire pour le rendu des champs : renvoie les attributs associés à un champ du formulaire
  const getFormFieldData = computed(
    () => (fieldName) => getFieldData(formSchema.value, fieldName)
  )
  provide(formContextInjectionKey, {
    getFormFieldData: getFormFieldData,
    formSchema: formSchema,
    setFieldValue: (key, value) => {
      setFieldValue(key, value)
      setFieldTouched(key, true)
    },
  })
  const sendTestRunning = ref(false)
  const onSendTestEmail = async () => {
    setTouched({
      smtp_host: true,
      smtp_port: true,
      smtp_user: true,
    })
    sendTestRunning.value = true

    const { valid, errors } = await validate()
    if (valid) {
      console.log('Form is valid, sending test email...')
      const rawValues = toRaw(values)
      mailTestResult.value = await store.sendTestEmail(rawValues)
    } else {
      setErrors(errors)
    }
    sendTestRunning.value = false
  }
  const onSenderEmailChange = (value, isValid) => {
    // Ici on pourrait fecther les configurations SMTP à partir de l'e-mail saisi
    if (isValid) {
      showDetails.value = true
      helpMessage.value = ''
    } else {
      showDetails.value = false
      helpMessage.value = false
    }
  }
  const onCancel = () => {
    emit('cancel')
  }
</script>

<template>
  <Layout @submitForm="onSubmit">
    <template v-slot:title>
      Configuration du service d'envoi d'e-mails
    </template>
    <template v-slot:fields>
      <div
        class="alert alert-info"
        role="alert"
        v-if="helpMessage"
        v-html="helpMessage"
      ></div>
      <div class="alert alert-warning" role="alert" v-if="!canTestEmail">
        L'envoi d'e-mail de test est uniquement possible si un e-mail est
        renseigné pour votre compte
      </div>
      <div class="limited_width width40">
        <FormErrorList :errors="errors" :getFormFieldData="getFormFieldData" />
        <SmtpSettingsFormContent
          :show-details="showDetails"
          @change:sender_email="onSenderEmailChange"
        />
      </div>
    </template>
    <template v-slot:buttons>
      <div class="limited_width width40" v-if="mailTestResult">
        <div :class="'alert alert-' + mailTestResult.status" role="alert">
          {{ mailTestResult.message }}
        </div>
      </div>
      <div class="limited_width width40" v-if="showDetails">
        <button
          id="deformsubmit"
          name="submit"
          type="submit"
          class="btn btn-primary"
          value="submit"
          :disabled="isSubmitting || sendTestRunning"
        >
          <Icon name="check" />
          {{ isEdit ? 'Enregistrer' : 'Sauvegarder' }}
        </button>
        <button
          type="button"
          class="btn btn-default"
          :disabled="!canTestEmail || isSubmitting || sendTestRunning"
          @click="onSendTestEmail"
          title="Un e-mail de test sera envoyé à l'adresse e-mail de votre compte utilisateur"
        >
          <Icon name="loading" v-if="sendTestRunning" />
          <Icon name="envelope" v-else />
          Envoi d'un e-mail de test
        </button>
        <button
          id="deformcancel"
          name="cancel"
          type="button"
          class="btn btn-"
          @click="onCancel"
        >
          <Icon name="times" />
          Annuler
        </button>
        <DebugContent v-if="debug" :debug="values" />
      </div>
    </template>
  </Layout>
</template>
