/* Store pinia pour la configuration SMTP

Ajout

    Si companyId est vide c'est qu'on est niveau CAE
    Sinon on est niveau entreprise

Modification
   On récupère les settings par Id depuis l'API
   On update les settings via l'API
*/

import { defineStore } from 'pinia'
import api from '@/api/index'
import getFormConfigStore from './formConfig'

export const useSmtpConfigStore = getFormConfigStore('smtpSettings')

export const useSmtpSettingsStore = defineStore('smtpSettings', {
  state: () => ({
    companyId: null, // Optional company ID to which the settings belong
    currentSettingsId: null, // Optional current setting ID to edit
    smtpSettings: null,
  }),
  actions: {
    async setInitialValues({companyId, smtpSettingsId, defaultEmail}) {
        this.companyId = companyId
        this.smtpSettingsId = smtpSettingsId
        if (this.smtpSettingsId) {
            await this.loadSmtpSettings()
        } else if (defaultEmail){
          this.smtpSettings = {
              sender_email: defaultEmail,
              smtp_user: defaultEmail,
          }
        }
    },
    async loadSmtpSettings() {
      this.smtpSettings = await api.smtpSettings.load(this.smtpSettingsId)
    },
    async saveSmtpSettings(settings) {
      if (this.smtpSettingsId) {
        this.smtpSettings = await api.smtpSettings.update(settings, this.smtpSettingsId, )
      } else {
        api.smtpSettings.setCompanyId(this.companyId)
        this.smtpSettings = await api.smtpSettings.create(settings)
      }
      return this.smtpSettings
    },
    async sendTestEmail(settings){
      return await api.smtpSettings.sendTestEmail(settings)
    },
    async deleteSmtpSettings() {
      if (this.smtpSettingsId) {
        await api.smtpSettings.delete(this.smtpSettingsId)
      }
      this.smtpSettings = null
      this.smtpSettingsId = null
    }
  },
})
