<script setup>
  import { Suspense } from 'vue'
  import { collectOptions } from '@/helpers/context'
  import SmtpSettingsComponent from '@/components/smtp/SmtpSettingsComponent.vue'

  const options = collectOptions()

  const redirectOnsave = () => {
    if (options.companyId) {
      window.location.replace('/companies/' + options.companyId)
    } else {
      window.location.replace('/admin/main/smtp')
    }
  }
  const redirectOnCancel = redirectOnsave
</script>

<template>
  <Suspense>
    <div>
      <SmtpSettingsComponent
        :companyId="options.company_id"
        :apiUrl="options.api_url"
        :smtpSettingsId="options.smtp_settings_id"
        :defaultEmail="options.default_email"
        :formConfigUrl="options.form_config_url"
        @saved="redirectOnsave"
        @cancel="redirectOnCancel"
      />
    </div>
    <template #fallback> Chargement... </template>
  </Suspense>
</template>
