<script setup>
  import { inject } from 'vue'
  import * as yup from 'yup'
  import { useFormValues } from 'vee-validate'

  import { formContextInjectionKey } from '@/helpers/form'
  import Input from '@/components/forms/Input.vue'
  import BooleanCheckbox from '../forms/BooleanCheckbox.vue'

  const props = defineProps({
    showDetails: { type: Boolean, default: false },
  })
  const values = useFormValues()
  console.log(values.value)
  const emit = defineEmits(['change:sender_email'])
  const { getFormFieldData, setFieldValue } = inject(formContextInjectionKey)
  async function onSenderEmailChange(value) {
    const isValid = await yup.string().email().isValid(value)
    if (isValid) {
      setFieldValue('smtp_user', value)
    }
    emit('change:sender_email', value, isValid)
  }
  // const values = useFormValues()
  const onSenderEmailBlur = async (value) => {
    const isValid = await yup.string().email().isValid(value)
    if (isValid) {
      setFieldValue('smtp_user', value)
    }
    emit('change:sender_email', value, isValid)
  }
</script>
<template>
  <fieldset>
    <div class="row form-row">
      <div class="col-md-12">
        <Input
          v-bind="getFormFieldData('sender_email')"
          @change-value="onSenderEmailChange"
          @blur-value="onSenderEmailBlur"
        />
      </div>
    </div>
    <div class="row form-row">
      <div class="col-md-8">
        <Input v-bind="getFormFieldData('smtp_host')" :editable="showDetails" />
      </div>
      <div class="col-md-4">
        <Input v-bind="getFormFieldData('smtp_port')" :editable="showDetails" />
      </div>
    </div>

    <div class="row form-row">
      <div class="col-md-8">
        <Input v-bind="getFormFieldData('smtp_user')" :editable="showDetails" />
      </div>
      <div class="col-md-4">
        <Input
          v-bind="getFormFieldData('smtp_password')"
          :type="'password'"
          autocomplete="new-password"
          :editable="showDetails"
        />
      </div>
    </div>
    <div class="row form-row">
      <div class="col-md-6">
        <BooleanCheckbox
          v-bind="getFormFieldData('smtp_ssl')"
          :editable="showDetails"
        />
      </div>
      <div class="col-md-6">
        <BooleanCheckbox
          v-bind="getFormFieldData('smtp_tls')"
          :editable="showDetails"
        />
      </div>
    </div>
  </fieldset>
</template>
